.video {
  position: relative;
  display: flex;
  height: 480px;
  background-color: $gray2a;
  margin-top: 12rem;
  overflow: hidden;
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 411px;
    text-align: center;
    margin: auto; }
  &__title {
    font-size: 2.8rem;
    line-height: 1.5;
    margin-bottom: 1.9rem;
    z-index: 2; }
  &__txt {
    font-size: 1.8rem;
    line-height: 1.56;
    z-index: 2; } }
