
@import "config";
@import "media";
@import "mixins";
@import "reset";
@import "vars";
@import "blocks/all_blocks";

* {
  box-sizing: border-box; }

html {
  font-size: 10px; }

body {
  font-size: 1.8rem;
  font-family: $Nunito;
  font-weight: normal;
  color: $gray4; }

h1,
h2,
h3 {
  font-family: $Poppins;
  font-weight: bold;
  color: $darkViolet; }

.link {
  color: $darkViolet;
  font-size: 1.6rem;
  text-decoration: none;
  &:hover {
    text-shadow: 0 0 1.2px $black; } }

.link-arrow {
  &::after {
    content: "\f30b";
    font-family: FontAwesome;
    margin-left: 1rem; } }

hr {
  margin-bottom: 3rem; }

.step {
  margin: 3rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 35rem; }

.titleBody {
  font-weight: bold;
  font-size: 3.6rem;
  text-align: center;
  margin: 3rem 0; }
