%wrapper {
  margin: 0 auto;
  display: none;
  @include mediaSize($phoneVertical) {
    display: block;
    max-width: 300px; }
  @include mediaSize($phone) {
    max-width: 550px; }
  @include mediaSize($tablet) {
    max-width: 750px; }
  @include mediaSize($notebook) {
    max-width: 950px; }
  @include mediaSize($desktop) {
    max-width: 1000px; }
  @include mediaSize($projector) {
    max-width: 1146px; } }

%container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center; }

.wrapper {
  @extend %wrapper; }

.active {
  display: block; }

.hide {
  display: none; }

.visible {
  opacity: 1; }

.textAlign_cen {
  display: flex;
  justify-content: center;
  text-align: center; }

.mr30 {
  margin-right: 3rem;
  @include mediaSize($phone) {
    margin-right: 28rem; }
  @include mediaSize($tablet) {
    margin-right: 3rem; } }

.mr50 {
  margin-top: 5rem;
  @include mediaSize($projector) {
    margin-top: 0; } }
