.stage {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  @include mediaSize($tablet) {
    flex-direction: row; }
  &__content {
    position: relative;
    top: -7rem; }
  &__title {
    font-size: 3rem;
    line-height: 1.2;
    text-align: center;
    margin-top: 5rem;
    @include mediaSize($phone) {
      font-size: 4rem;
      margin-top: 2rem; }
    @include mediaSize($tablet) {
      font-size: 5rem;
      text-align: left;
      margin-top: 0; } }
  &__txt {
    font-size: 1.6rem;
    text-align: center;
    line-height: 1.5;
    padding: 3.2rem 0 4rem;
    @include mediaSize($phone) {
      font-size: 2rem; }
    @include mediaSize($tablet) {
      font-size: 2.2rem;
      text-align: left; } }
  &__img {
    order: -1;
    width: 100%;
    @include mediaSize($tablet) {
      order: 0;
      width: auto; } } }
