.call-to-action {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  @include mediaSize($tablet) {
    flex-direction: row; }
  &__content {
    @include mediaSize($tablet) {
      width: 50%; } }
  &__title {
    font-size: 2.4rem;
    line-height: 1.5;
    text-align: center;
    margin-top: 5rem;
    @include mediaSize($phone) {
      font-size: 2.8rem;
      margin-top: 2rem; }
    @include mediaSize($tablet) {
      text-align: left;
      margin-top: 0; } }
  &__txt {
    font-size: 1.6rem;
    text-align: center;
    line-height: 1.56;
    padding: 3.1rem 0 4.8rem;
    @include mediaSize($phone) {
      font-size: 1.8rem; }
    @include mediaSize($tablet) {
      text-align: left; } }
  &__img {
    order: -1;
    width: 100%;
    @include mediaSize($tablet) {
      order: 0;
      width: auto; } } }

.call-to-action-reverse {
  padding: 13.4rem 0 9rem;
  @include mediaSize($tablet) {
    flex-direction: row-reverse; } }
