.skills {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 14rem 0;
  @include mediaSize($projector) {
    flex-direction: row;
    text-align: left; }
  &__content {
    flex-shrink: 0;
    @include mediaSize($projector) {
      width: 50%; } }
  &__title {
    font-size: 3.4rem; }
  &__txt {
    width: 70%;
    font-size: 1.8rem;
    color: $gray;
    line-height: 1.5;
    padding: 3.2rem 0 6.4rem;
    margin: 0 auto;
    @include mediaSize($projector) {
      width: 554px;
      margin: 0; } }
  &__items {
    width: 70%;
    flex-shrink: 0;
    margin-top: 8rem;
    @include mediaSize($projector) {
      width: 50%;
      margin-top: 0; } }
  &__item {
    margin-bottom: 3rem; }
  &__item-txt {
    display: flex;
    justify-content: space-between; }
  &__name {
    color: $gray4;
    font-weight: 400;
    font-size: 1.8rem; }
  &__item-view {
    width: 100%;
    height: 6px;
    margin-top: 1rem;
    border-radius: 2.5px;
    background-color: rgba(225, 228, 237, 0.6); }
  &__item-view-percent {
    height: 100%;
    background-color: $violet; } }
