.header-general {
  padding: 2.4rem 0 3.6rem;
  @include mediaSize($tablet) {
    padding-bottom: 10.6rem; } }
.top-header {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  text-align: center;
  font-size: 1.6rem;
  color: $gray;
  padding-bottom: 1.7rem;
  @include mediaSize($phone) {
    flex-direction: row; }
  &__phone {
    display: block;
    color: $gray;
    text-decoration: none;
    margin-top: 1rem;
    @include mediaSize($phone) {
      margin-top: 0; } } }

.divider {
  border: none;
  border-bottom: 1px solid $gray3a;
  margin: 0; }

.header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  @include mediaSize($notebook) {
    flex-direction: row; }
  &__navbar {
    display: flex;
    align-items: center;
    margin: 1.5rem 0 1rem 25rem;
    @include mediaSize($phone) {
      margin: 0; } }
  &__logo-image {
    display: block;
    @include mediaSize($phone) {
      margin-right: 5rem; } }
  &__menu {
    display: none;
    @include mediaSize($phone) {
      display: flex; } }
  &__submenu {
    display: none;
    position: absolute;
    top: 5.5rem;
    left: -3rem;
    padding: 3rem;
    width: 165px;
    border-radius: 10px;
    box-shadow: 0 0 20px 0 $gray2;
    background-color: $white;
    z-index: 1;
    @include mediaSize($tablet) {
      width: 235px; }
    .header__link:not(:last-child) {
      margin-bottom: 2rem; } }
  &__menu-item {
    position: relative;
    margin-right: 3rem;
    padding: 2.3rem 0;
    &::before {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      content: "";
      width: 120%;
      height: 2px;
      background: linear-gradient(0.77deg, $violet 16.67%, $pink 100%);
      mix-blend-mode: normal;
      opacity: 0.1; }
    &:hover::before {
      display: block; }
    &:hover .header__submenu {
      display: block; } }
  &__link {
    position: relative;
    display: block;
    font-size: 1.6rem;
    color: $gray4;
    text-decoration: none;
    &:hover {
      color: $darkViolet;
      text-shadow: 0 0 1.2px $darkViolet; } }
  &__link_active {
    font-weight: bold; }
  &__account {
    display: flex;
    align-items: center;
    @include mediaSize($phone) {
      order: -1;
      margin-top: 3rem; }
    @include mediaSize($notebook) {
      order: 0;
      margin-top: 0; } }
  &__or {
    padding: 0 0.8rem; } }
