.circle {
  position: absolute;
  border-radius: 50%;
  &__video-left {
    width: 50px;
    height: 50px;
    top: 49px;
    left: 365px; }
  &__video-left-big {
    width: 181px;
    height: 181px;
    top: 68px;
    left: 26px; }
  &__video-right-big {
    width: 215px;
    height: 215px;
    right: -108px;
    top: 109px; }
  &__video-middle {
    width: 37px;
    height: 37px;
    top: 49px;
    left: 65%; }
  &__video-right {
    width: 80px;
    height: 80px;
    bottom: 86px;
    right: 25%; } }

.circle_violet {
  background-color: $pink; }

.circle_violet-dark {
  background-color: $violet; }

.footer {
  &__circle_violet {
    width: 6rem;
    height: 6rem;
    right: -7rem;
    bottom: 0; }
  &__circle_dark-small {
    width: 2rem;
    height: 2rem;
    bottom: 0;
    right: 0; }
  &__circle_dark-big {
    width: 10rem;
    height: 10rem;
    top: 0;
    right: -18rem; }
  &__circle_dark-hidden {
    width: 15rem;
    height: 15rem;
    left: 48%;
    bottom: -20rem; }
  &__big-oval {
    position: absolute;
    left: -37rem;
    top: -20rem;
    z-index: -1;
    width: 485px;
    height: 1213px;
    background: $violet;
    border-radius: 242.5px;
    transform: rotate(-136deg); } }

.oval {
  position: absolute;
  transform: rotate(45deg);
  &__video-left {
    width: 185px;
    height: 611px;
    left: 50px;
    bottom: -435px;
    border-radius: 50%/12%; } }

.oval_dark {
  background-color: $violet; }

.action-full__circle-right {
  width: 50px;
  height: 50px;
  z-index: 0;
  right: 10%;
  top: 1.4rem; }

.action-full__circle-violet-right {
  width: 111px;
  height: 111px;
  bottom: -20px;
  right: 2%; }

.action-full-oval {
  width: 204px;
  height: 487px;
  bottom: -432px;
  border-radius: 50%/20%;
  @include mediaSize($phone) {
    bottom: -417px; }
  @include mediaSize($tablet) {
    bottom: -380px; } }
