.services {
  @extend %container;
  flex-direction: column-reverse;
  @include mediaSize($projector) {
    flex-direction: row; }
  &__cards {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    margin-top: 6rem;
    .infoblock:nth-child(2n) {
      margin-left: 3rem;
      margin-top: 3rem; }
    @include mediaSize($projector) {
      justify-content: flex-start;
      margin-top: 0; } }
  &__content {
    text-align: center;
    flex-shrink: 0;
    @include mediaSize($tablet) {
      width: 70%; }
    @include mediaSize($projector) {
      width: 330px; } }
  &__title {
    font-size: 3.4rem;
    line-height: 1.35; }
  &__txt {
    font-size: 1.8rem;
    line-height: 1.56;
    padding: 3rem 0 4.7rem; } }
