// INFOBLOCKS
.infoblock {
  width: 100%;
  height: 229px;
  border-radius: 10px;
  box-shadow: 0 0 20px 0 $gray2;
  background-color: $white;
  padding: 3rem 3rem 2.8rem;
  margin-bottom: 4rem;
  &:last-child {
    margin-bottom: 0; }
  @include mediaSize($tablet) {
    width: 360px; }
  @include mediaSize($projector) {
    margin: 0; }
  &__title {
    font-size: 2.2rem;
    line-height: 1.5;
    padding: 3rem 0 1.9rem; }
  &__txt {
    line-height: 1.5;
    color: $gray; }
  &__link {
    display: none;
    margin-top: 3rem; }
  &:hover {
    box-shadow: 0 0 20px 0 $violet2;
    background-image: linear-gradient(to top, $violet, $pink); }
  &:hover .infoblock__icon {
    display: none; }
  &:hover .infoblock__title {
    padding-top: 0;
    color: $white; }
  &:hover .infoblock__txt {
    color: $white; }
  &:hover .infoblock__link {
    display: inline-block;
    color: $white; } }

// PRICES
.price {
  width: 360px;
  padding: 6rem;
  border-radius: 10px;
  box-shadow: 0 0 20px 0 $gray2;
  background-color: $white;
  position: relative;
  &__name {
    font-size: 2rem;
    line-height: 1.5;
    margin-bottom: 1.9rem; }
  &__divider {
    width: 40px;
    height: 2px;
    border-radius: 2px;
    background-color: $pink; }
  &__sum {
    font-family: $Poppins;
    font-size: 5rem;
    font-weight: bold;
    line-height: 1.2;
    margin-top: 0.9rem;
    margin-bottom: 1rem; }
  &__period {
    opacity: 0.8;
    font-size: 1.6rem; }
  &__features {
    padding: 6rem 0; }
  &__feature-item {
    color: $gray;
    line-height: 1.5;
    margin-bottom: 1rem;
    &::before {
      content: "\f00c";
      font-family: FontAwesome;
      color: $violet;
      margin-right: 1rem; } }
  &__feature-item_minus {
    &::before {
      content: "\f00d";
      font-family: FontAwesome;
      color: $coral;
      margin-right: 1rem;
      opacity: 0.5; } } }

.price_best::before {
  position: absolute;
  right: 6rem;
  top: 6.4rem;
  content: "best choice";
  text-transform: uppercase;
  font-size: 1rem;
  color: $coral;
  background-color: $gray3;
  border-radius: 13px;
  padding: 0.6rem 1rem; }

// HELPERS
.helper {
  padding-left: 6.2rem;
  position: relative;
  margin-bottom: 6rem;
  &:last-child {
    margin-bottom: 0; }
  @include mediaSize($projector) {
    margin-bottom: 0; }
  &__question {
    &::before {
      position: absolute;
      left: 0;
      top: 0;
      content: "\f059";
      font-family: FontAwesome;
      font-size: 3.2rem;
      color: $violet; } }
  &__support {
    &::before {
      position: absolute;
      left: 0;
      top: 0;
      content: "\f2a0";
      font-family: FontAwesome;
      font-size: 3.2rem;
      color: $violet; } }
  &__title {
    font-size: 2rem;
    line-height: 1.5;
    margin-bottom: 2rem; }
  &__txt {
    color: $gray;
    line-height: 1.56;
    padding: 2rem 0 3rem;
    @include mediaSize($phone) {
      width: 493px; } } }
