footer {
  padding: 20rem 0 8rem;
  overflow: hidden; }

.footer {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: baseline;
  position: relative;
  @include mediaSize($desktop) {
    justify-content: space-between;
    flex-wrap: nowrap; }
  &__contact {
    color: $white;
    .footer__title {
      color: $white; } }
  &__title {
    font-size: 2rem;
    margin-bottom: 3rem; }
  &__info {
    padding-left: 7rem;
    position: relative;
    &::before {
      position: absolute;
      left: 0;
      top: 0;
      content: "\f2a0";
      font-family: FontAwesome;
      font-size: 4.2rem; } }
  &__info-phone {
    text-decoration: none;
    font-size: 2.2rem;
    font-family: $Poppins;
    font-weight: bold;
    line-height: 2;
    color: $white; }
  &__address {
    line-height: 1.5;
    padding: 3rem 0 1rem;
    text-shadow: $violet 2px 1px 0;
    @include mediaSize($phone) {
      width: 36rem; } }
  &__email {
    line-height: 1.5;
    text-decoration: none;
    color: $white; }
  &__links {
    margin-top: 6rem;
    order: 3;
    padding-left: 2.3rem;
    @include mediaSize($phone) {
      padding-left: 0; }
    @include mediaSize($desktop) {
      order: 0; } }
  &__links-items {
    display: flex;
    flex-flow: row wrap;
    width: 320px; }
  &__link-items {
    display: block;
    width: 130px;
    text-decoration: none;
    line-height: 1.5;
    &:nth-child(2n+1) {
      margin-right: 6rem; }
    &:nth-child(n+3) {
      margin-top: 1rem; } }
  &__divider {
    width: 99px;
    height: 4px;
    background-color: $gray2;
    margin-top: 7rem; }
  &__copyright {
    font-size: 1.6rem;
    margin-top: 1.3rem;
    line-height: 1.5;
    color: $gray; }
  &__subscribe {
    margin-top: 6rem;
    @include mediaSize($desktop) {
      margin-top: 0; } }
  &__subscribe-form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 299px;
    height: 50px;
    border-radius: 25px;
    border: solid 1px $violet;
    overflow: hidden;
    margin-bottom: 3rem; }
  &__text-field {
    border: none;
    padding: 1.1rem 0 1.2rem 2.4rem;
    line-height: 1.5;
    color: $gray;
    font-size: 1.8rem;
    outline: none; }
  &__form-btn {
    background-color: $transparent;
    color: $violet;
    font-size: 1.6rem;
    margin-right: 2.1rem;
    border: none;
    outline: none;
    cursor: pointer; }
  &__subscribe-txt {
    width: 263px;
    opacity: 0.8;
    line-height: 1.5;
    color: $gray; } }
