.actions-full {
  position: relative;
  background-color: $gray2a;
  // height: 17rem
  overflow: hidden; }

.action-full {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  height: 17rem;
  @include mediaSize($projector) {
    flex-direction: row;
    justify-content: space-between; }
  &__title {
    font-size: 2.7rem;
    z-index: 2; } }
